import React ,{ Suspense } from "react";
import AuthMiddleware from "../middleware/AuthMiddleware";
import GuestMiddleware from "../middleware/GuestMiddleware";
import {Routes, Route } from "react-router-dom";
import Error404 from "../404";
import AppLoading from "../components/loading/AppLoading";

const App = React.lazy(() => import("../App"));
const  HomeGuest= React.lazy(() => import("../pages/guest/Home"));

const MainRoutes = () => {
    return(
        <Routes>
                                                
                {/* rotas publicas */}  
                <Route  element={<GuestMiddleware />}>
                        <Route path="alex-emily-casamento/:id"  element={<Suspense fallback={<AppLoading />}><App /></Suspense>}>
                                <Route path="" element={<HomeGuest />} />
                        </Route>
                </Route>
        
                {/* rotas  privadas */}
                <Route element={<AuthMiddleware />}>
                        <Route path="/dashboard"element={<h2 className="mt-5 mx-5">Você está autencaticado!<hr /></h2>}></Route> 
                </Route>
                
                {/*  rota 404 */}
                <Route path="*" element={<Error404 />} />

        </Routes>
    );
}

export default MainRoutes;
